ul {
  list-style-type: none;
  margin: 10px 0px 0px -10px;
  padding: 0;
  overflow: hidden;
  background-color: #ffffff;
  align-content: start;
  justify-self: start;
  width: 100%;
}

li {
  float: left;
}

.text {
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
}
